import React from "react";
import myImg from '../../images/my-img.jpg';

export const Intro = () =>{
    return(
        <div >

        <h2 className="major">Introduction</h2>
        <span className="tab-container">
          <img src={myImg} alt="" height="25%" width="40%" style={{ display: 'block', marginLeft: 'auto', marginRight: 'auto', width: '50%' }} />
        </span>

        <p>
          Hey There!
        </p><p> Im a Tech-savvy developer with 6.7 years of experience
          for collaborating and working on multiple web-based projects. Passionate, hardworking coder with penchant for developing customized interfaces that factor in unique demands for accessibility, reachability and security. Organized approach to meeting multiple, concurrent deadlines.
        </p>
        <p>I am Passionate for web development. My best assets are my creativity, my feeling with technology and my passion.
          I possess a unique blend of technical expertise and business acumen. My expertise includes coding in React JS, Javascript, Typescript and I thrive doing test-driven development. I am passionate about delivering high-quality solutions that meet and exceed client expectations.
        </p>

        <p>I completed my Bachelor's of technology in Computer Sciences from Kurukshetra university with an honour's degree
          in 2018.
        </p>
      </div>
    )
}